.group-list-container {
  color: whitesmoke;
  border: 1px solid white;
  border-radius: 5%;
  width: 260px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 55px;
  padding: 5px 0 5px;
  background-color: rgb(34, 33, 33);
  align-content: center;
}
.group-list-section {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.group-list-section button {
  border: 1px grey solid;
  margin: 5px 3px 5px;
  color: rgb(240, 233, 233);
  height: 50px;
}
.select-team-section {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  width: 100%;
}

.user-container-search .add-user {
  vertical-align: top;
}

.user-team-container {
  display: flex;
  flex-direction: row;
  border-bottom: 1px solid rgb(133, 130, 130);
  width: 90%;
  justify-content: space-around;
  align-items: center;
  margin: auto;
}
.group-users-zone p {
    text-align: center;
}
.group-battle-controler{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 15px;
}
.group-users-zone{
    width: 100%;
    height: 100%;
}


.fight-result-controler{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 15px;

}

@media only screen and (max-width: 970px) {
  .group-list-section{
      flex-direction: column;
      align-items: center;
  }
  .select-team-section{
    flex-direction: column;
  }

  .group-battle-controler .MuiSvgIcon-root{
    transform: rotate(90deg) !important;
  }
}