.popular-container {
  width: 92%;
  margin: auto;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  opacity: 0.9;
}
.popular-item {
  color: whitesmoke;
  border: 1px solid white;
  border-radius: 5%;
  min-width: 250px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 15px;
  padding: 5px 0 5px;
  background-color: rgb(34, 33, 33);
  align-content: center;
}

.selectedItem {
  border-bottom: 2px solid red !important;
  
  font-weight: 600;
  transform: scale(1.02);
  color: rgb(255, 255, 255) !important;
}

.popular-item a {
  text-decoration: none;
  color: rgb(149, 165, 149);
  font-weight: 600;
}
.popular-item:hover {
  transform: scale(1.02);
}

.popular-input{
  margin-left: 5px;
  margin-top: 5px;
  border: 1px grey solid;
  padding-left: 10px;
  height: 48px;
  min-width: 250px;
  color: rgb(240, 233, 233) !important;
}